
































export default {
	data() {
		return {
			status:true,
			balance:0,
			// 用户数据
			money:0
		};
	},
	onLoad(option) {
		this.balance = option.bala
	},
	onShow() {
	},
	methods:{
		getMoney(e){
			this.money = e.detail.value
		},
		cz(){
			if(this.money == 0){
				uni.showToast({
					title:'请输入金额!',
					icon:'none'
				})
			}else{
				var arr = {
					money:this.money,
					type:this.status?2:3
				}
				arr = JSON.stringify(arr)
				uni.navigateTo({
					url: '../pw/pw?arr='+arr,
				});
			}
		}
	}
}
